a {
  color: #F24747;
  transition: border 0.3s ease, background 0.3s ease, color 0.3s ease;
}
a:hover, a:focus {
  color: #000;
  text-decoration: underline;
}
a [class^=icon-],
a [class*=" icon-"] {
  text-decoration: none;
}

.nav-link {
  display: inline-block;
  color: white;
  padding: 0 12px;
  height: 40px;
  transition: background 0.3s;
}
.nav-link:active {
  transform: scale(0.98);
}
.nav-link.mono {
  font-family: monospace;
}

.nav__item {
  display: inline-block;
  line-height: 40px;
  margin: 0;
  padding: 0;
  margin-right: 6px;
  border-bottom: rgba(255, 255, 255, 0);
}
.nav__item.active .nav-link {
  font-weight: bold;
}
.nav__item .nav-link {
  position: relative;
}
.nav__item .nav-link:hover, .nav__item .nav-link:focus {
  color: #fff;
  background: rgba(255, 255, 255, 0.1);
  text-decoration: none;
}
@media only screen and (min-width: 700px) {
  .nav__item {
    margin-right: 0;
  }
  .nav__item .nav-link {
    height: 70px;
    line-height: 70px;
  }
}

.nav__item--right {
  position: absolute;
  top: 0;
  right: 0;
  margin-right: 0;
}
.nav__item--right a {
  height: 70px;
  line-height: 70px;
}
@media only screen and (min-width: 700px) {
  .nav__item--right {
    position: relative;
    flex: 1;
    display: flex;
    justify-content: flex-end;
  }
}
.nav__item--right .svg-icon {
  width: 24px;
  height: 24px;
}

.docs-nav {
  background: #0F2634;
}

.fixed-nav {
  border-top: 1px solid #1a435c;
  list-style: none;
  overflow-x: auto;
  white-space: nowrap;
  -webkit-overflow-scrolling: touch;
  margin-bottom: 0;
}
@media only screen and (min-width: 700px) {
  .fixed-nav {
    border-top: 0;
  }
}
.fixed-nav .nav-link {
  height: 50px;
  line-height: 50px;
}
.fixed-nav .nav-link:hover, .fixed-nav .nav-link:focus {
  color: #fff;
  background: rgba(255, 255, 255, 0.1);
  text-decoration: none;
}

.fixed-nav__item {
  display: inline-block;
}
.fixed-nav__item.active .nav-link {
  font-weight: bold;
}
.fixed-nav__item a {
  color: #fff;
}

.sticky-nav {
  border-radius: 3px;
  padding: 12px;
  background: rgba(255, 255, 255, 0.99);
  border: 1px solid #ECECEC;
  border-top: 3px solid #F24747;
  position: absolute;
  width: 100%;
  transition: opacity 0.3s;
  height: 0;
  opacity: 0;
  overflow: hidden;
}
.open .sticky-nav {
  position: relative;
  height: auto;
  opacity: 1;
}
@media only screen and (min-width: 700px) {
  .sticky-nav {
    position: relative;
    opacity: 1;
    height: auto;
  }
}

.sticky-nav__items {
  list-style: none;
  margin-bottom: 0;
}
.sticky-nav__items .sticky-nav__item:last-child .nav-link {
  border-bottom: 0;
}

.sticky-nav__item {
  list-style: none;
}
.sticky-nav__item .nav-link {
  color: #0F2634;
  width: 100%;
  height: auto;
  padding: 12px 0;
  border-bottom: 1px solid #f9f9f9;
  position: relative;
}
@media only screen and (min-width: 700px) {
  .sticky-nav__item .nav-link {
    padding: 4px 0;
  }
}

.doc-wrapper {
  position: relative;
}

.doc-nav {
  display: none;
  height: 0;
  position: relative;
}
@media only screen and (min-width: 700px) {
  .doc-nav {
    display: block;
  }
}
.doc-nav.open {
  height: auto;
}
.nav-expand {
  width: 40px;
  height: 40px;
  display: block;
  border: 3px solid #0F2634;
  border-radius: 50%;
  position: absolute;
  right: 12px;
  top: 12px;
  background: rgba(255, 255, 255, 0.9);
  z-index: 1;
}
.nav-expand .svg-icon {
  transition: all 0.2s;
  position: absolute;
  top: 2px;
  left: 2px;
  font-size: 30px;
}
.nav-expand .svg-icon.close {
  left: 2px;
  opacity: 0;
  transform: scale(0.5);
}
@media only screen and (min-width: 700px) {
  .nav-expand {
    display: none;
  }
}
.nav-expand:hover {
  cursor: pointer;
}
.open .nav-expand {
  border-color: #F24747;
}
.open .nav-expand .svg-icon.open {
  opacity: 0;
  transform: scale(0.5);
}
.open .nav-expand .svg-icon.close {
  color: #F24747;
  opacity: 1;
  transform: scale(1);
}

